




































































import { Component, Prop, PropSync, Vue } from "vue-property-decorator";
import { Repository } from "@/api/models/Repository";
import { ColorUtil } from "@/util/ColorUtil";

@Component
export default class RepositoriesTable extends Vue {
    /**
     * Repositories to display.
     */
    @Prop()
    repositories: Repository[];

    /**
     * Table search value.
     */
    @PropSync("search")
    tableSearch: string;

    /**
     * Edit repositories action.
     */
    @Prop()
    editAction: () => void;

    /**
     * Table headers.
     */
    tableHeaders = [
        {
            value: "image",
            sortable: false,
            filterable: false,
            align: "center",
            width: "1%"
        },

        {
            text: "Repository",
            value: "name"
        }
    ];

    /**
     * Open the selected repository.
     * @param repository Repository to open.
     */
    openRepository(repository: Repository) {
        if (this.$vuetify.breakpoint.smAndDown) {
            window.open(repository.url, "_blank");
        }
    }

    /**
     * Get the color for a given repository based on the name.
     * @param repository Repository
     */
    getRepositoryColor(repository: Repository): string {
        return ColorUtil.getColorFromString(repository.name);
    }
}
